.formController {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: #000; */
  /* height: 100vh; */
  margin: 0;
  padding: 0;
}
.formController .formContainer {
  display: flex;
  justify-content: center;
  /* width: 50%; */
  margin: 5rem 0;
  background-color: rgb(86, 118, 146);
  padding: 5rem;
  border-radius: 10px;
}
.formContainer input {
  width: 300px;
  padding: 8px 3px;
  margin: 15px;
  border-radius: 3px;
}
.formContainer label {
  color: #fff;
}
.formContainer .category {
  width: 310px;
  padding: 8px;
  border-radius: 3px;
  margin: 15px;
}
.addBtn {
  padding: 8px 30px;
  background-color: #000;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-weight: 300;
  cursor: pointer;
  margin-left: 1rem;
}
.backbtn {
  background-color: #000;
  padding: 8px 30px;
  border-radius: 4px;
  font-weight: 300;
  cursor: pointer;
  margin-left: 1rem;
}

@media screen and (max-width: 705px) {
  .formController .formContainer{
    flex-direction: column;
    padding: 2rem;
  }
  .formController h1{
    text-align: center;
  }
}
